import iView from 'iview';
import store from '../store/index';
import router from './index';
import {getToken} from '../utils/token';
import Util from '../libs/util';
import Cookies from 'js-cookie';

/**
 * (main.js)
 * 
 * 1. 
 * 2. 
 * 3. 
 * 4. 
 */

const whiteList = ['/login']; // 
router.beforeEach((to, from, next) => {
    iView.LoadingBar.start();
    Util.title(to.meta.title);
    // 1. 
    if (Cookies.get('locking') === '1' && to.name !== 'locking') {
        next({replace: true, path: '/locking'});
    } else if (Cookies.get('locking') === '0' && to.name === 'locking') {
        next(false);
    } else {
        // 2. 
        if (getToken()) {
            if (to.path === '/login') {
                next('/home');
            } else {
                if (store.getters.nickname === '') {
                    // 
                    store.dispatch('GetInfo').then((res) => {
                        // 
                        store.dispatch('GenerateRoutes', store.getters.menus).then(() => {
                            router.addRoutes(store.getters.permissionRouters); // 
                            next({...to});// hack addRoutes
                        });
                    }).catch(() => {
                        // 
                        store.dispatch('FedLogOut').then(() => {
                            next('/login');
                        });
                    });
                } else {
                    next();
                }
            }
        } else {
            if (whiteList.indexOf(to.path) !== -1) {
                next();
            } else {
                next('/login');
            }
        }
    }
});

router.afterEach((to) => {
    Util.openNewPage(router.app, to.name, to.params, to.query);
    iView.LoadingBar.finish();
    window.scrollTo(0, 0);
});



