import Vue from 'vue'
import Router from 'vue-router'
import Main from '../views/Main';

Vue.use(Router);

/**
 * , 
 * webapp, asyncRouterMap
 * @type {*[]}
 */
export const commonRouterMap = [
    {path: '/login', name: 'login', meta: {title: ' - Fastjee'}, component: () => import('@/views/login.vue')},
    {path: '/locking',name: 'locking',meta: {title: ' - Fastjee'},component: () => import('@/views/main-components/lockscreen/components/locking-page.vue')},
    {path: '/403',name: 'error-403', meta: {title: '403 - '},component: () => import('@/views/error-page/403.vue')},
    {path: '/500',name: 'error-500',meta: {title: '403 - '},component: () => import('@/views/error-page/500.vue')},
    {path: '/404',name: 'error-404',meta: {title: '404 - '},component: () => import('@/views/error-page/404.vue')},
    {
        path: '/',
        name: 'home',
        redirect: '/home',
        component: Main,
        hidden: true,
        children: [
            {path: 'home', title: {i18n: 'home'}, name: 'home_index', component: () => import('@/views/home/home.vue')}
        ]
    }
];

export default new Router({
    // mode: 'history', // 
    scrollBehavior: () => ({y: 0}),
    routes: commonRouterMap
})

/**
 * 
 * this.name = tb_resource.code
 * @type {*[]}
 */
export const asyncRouterMap = [
    {
        path: '/system',
        icon: 'gear-b',
        name: 'systemManager',
        title: '',
        component: Main,
        children: [
            {
                path: 'userManager',
                icon: 'person-stalker',
                name: 'userManager',
                title: '',
                component: () => import('@/views/system/userManager/index.vue')
            },
            {
                path: 'roleManager',
                icon: 'social-buffer',
                name: 'roleManager',
                title: '',
                component: () => import('@/views/my-components/text-editor/text-editor.vue')
            },
            {
                path: 'resourceManager',
                icon: 'navicon-round',
                name: 'resourceManager',
                title: '',
                component: () => import('@/views/my-components/text-editor/text-editor.vue')
            },
            {
                path: 'sectionManager',
                icon: 'network',
                name: 'sectionManager',
                title: '',
                component: () => import('@/views/my-components/text-editor/text-editor.vue')
            }
        ]
    },
    {
        path: '/examples',
        icon: 'social-buffer',
        name: 'iview-examples',
        title: 'iView',
        component: Main,
        children: [
            {
                path: 'text-editor',
                icon: 'compose',
                name: 'text-editor',
                title: '',
                component: () => import('@/views/my-components/text-editor/text-editor.vue')
            },
            {
                path: 'md-editor',
                icon: 'pound',
                name: 'md-editor',
                title: 'Markdown',
                component: () => import('@/views/my-components/markdown-editor/markdown-editor.vue')
            },
            {
                path: 'image-editor',
                icon: 'crop',
                name: 'image-editor',
                title: '',
                component: () => import('@/views/my-components/image-editor/image-editor.vue')
            },
            {
                path: 'draggable-list',
                icon: 'arrow-move',
                name: 'draggable-list',
                title: '',
                component: () => import('@/views/my-components/draggable-list/draggable-list.vue')
            },
            {
                path: 'area-linkage',
                icon: 'ios-more',
                name: 'area-linkage',
                title: '',
                component: () => import('@/views/my-components/area-linkage/area-linkage.vue')
            },
            {
                path: 'file-upload',
                icon: 'android-upload',
                name: 'file-upload',
                title: '',
                component: () => import('@/views/my-components/file-upload/file-upload.vue')
            },
            {
                path: 'count-to',
                icon: 'arrow-graph-up-right',
                name: 'count-to',
                title: '',
                component: () => import('@/views/my-components/count-to/count-to.vue')
            },
            {
                path: 'split-pane-page',
                icon: 'ios-pause',
                name: 'split-pane-page',
                title: 'split-pane',
                component: () => import('@/views/my-components/split-pane/split-pane-page.vue')
            },
            {
                path: 'artical-publish',
                title: '',
                name: 'artical-publish',
                icon: 'compose',
                component: () => import('@/views/form/article-publish/article-publish.vue')
            },
            {
                path: 'workflow',
                title: '',
                name: 'workflow',
                icon: 'arrow-swap',
                component: () => import('@/views/form/work-flow/work-flow.vue')
            },
            {
                path: 'dragableTable',
                title: '',
                name: 'dragable-table',
                icon: 'arrow-move',
                component: () => import('@/views/tables/dragable-table.vue')
            },
            {
                path: 'editableTable',
                title: '',
                name: 'editable-table',
                icon: 'edit',
                component: () => import('@/views/tables/editable-table.vue')
            },
            {
                path: 'searchableTable',
                title: '',
                name: 'searchable-table',
                icon: 'search',
                component: () => import('@/views/tables/searchable-table.vue')
            },
            {
                path: 'exportableTable',
                title: '',
                name: 'exportable-table',
                icon: 'code-download',
                component: () => import('@/views/tables/exportable-table.vue')
            },
            {
                path: 'table2image',
                title: '',
                name: 'table-to-image',
                icon: 'images',
                component: () => import('@/views/tables/table-to-image.vue')
            },
            {
                path: 'mutative-router',
                title: '',
                name: 'mutative-router',
                icon: 'link',
                component: () => import('@/views/advanced-router/mutative-router.vue')
            },
            {
                path: 'argument-page',
                title: '111',
                name: 'argument-page',
                icon: 'android-send',
                component: () => import('@/views/advanced-router/argument-page.vue')
            }
        ]
    }
];



