/* eslint-disable no-console */
import axios from 'axios';
import {getToken} from './token';
import {Notice, Message} from 'iview';

// axios
const service = axios.create({
    timeout: 15000 // 
});

// Request 
service.interceptors.request.use(config => {
    if (getToken()) {
        // token 
        config.headers['Authorization'] = 'Bearer ' + getToken();
    }
    return config;
}, error => {
    // Do something with request error
    console.log(error); // for debug
    Promise.reject(error);
});

// Response 
service.interceptors.response.use(
    (response) => {
        const ret = response.data;
        // 
        if (ret && ret.code && ret.code !== 200) {
            errorMessage(ret.message);
            return Promise.reject(ret); // promise, rejectpromisethen(),catch()
        }
        return response;
    },
    (error) => {
        const ret = error.response;

        // 
        if (ret.status === 400) {
            errorMessage(ret.data.error_description || error.message); // 
        } else if (ret.status === 403 || ret.status === 401) {
            errorMessage(ret.data.message); // 
        } else {
            errorMessage('[' + error.message + ']');
        }
        return Promise.reject(error);
    }
);

export function successMessage (msg) {
    Message.success(msg);
}

export function errorMessage (msg) {
    Notice.error({
        title: '',
        desc: msg,
        duration: 6
    });
}

export default service;



